  background-size:cover;
  }
.item-2{
  background:url('https://genk.mediacdn.vn/2019/8/12/anh-2-15655805281641592033330.png');
   background-size:cover;
}
.item-3{
  background:url('https://media-cdn.laodong.vn/Storage/NewsPortal/2020/2/29/787695/578692Fa75ba9ce4c5ab.jpg');
   background-size:cover;
}

*:target ~ .item-1 {
    opacity: 0;
  }

#item-1:target ~ .item-1 {
    opacity: 1;
  }

#item-2:target ~ .item-2, #item-3:target ~ .item-3 {
    z-index: 3;
    opacity: 1;
  }
