.pic-ctn {
    width: 100vw;
    height: 100px;
  }
  
  @-webkit-keyframes display {
    0% {
      -webkit-transform: translateX(200px);
              transform: translateX(200px);
      opacity: 0;
    }
    10% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
    20% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
    30% {
      -webkit-transform: translateX(-200px);
              transform: translateX(-200px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateX(-200px);
              transform: translateX(-200px);
      opacity: 0;
    }
  }
  
  @keyframes display {
    0% {
      -webkit-transform: translateX(200px);
              transform: translateX(200px);
      opacity: 0;
    }
    10% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
    20% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
    30% {
      -webkit-transform: translateX(-200px);
              transform: translateX(-200px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateX(-200px);
              transform: translateX(-200px);
      opacity: 0;
    }
  }
  
  .pic-ctn {
    position: relative;
    width: 100vw;
    height: 300px;
    margin-top: 15vh;
  }
  
  .pic-ctn > img {
    position: absolute;
    top: -70px;
    left: calc(50% - 100px);
    opacity: 0;
    -webkit-animation: display 7s infinite;
            animation: display 7s infinite;
  }
  
  img:nth-child(2) {
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
  }
  img:nth-child(3) {
    -webkit-animation-delay: 4s;
            animation-delay: 4s;
  }
  img:nth-child(4) {
    -webkit-animation-delay: 6s;
            animation-delay: 6s;
  }
  img:nth-child(5) {
    -webkit-animation-delay: 8s;
            animation-delay: 8s;
  }
