.swiper-container {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
  height: 650px;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 300px;
  height: 300px;
  -webkit-box-reflect: below 1px linear-gradient(transparent,transparent,#0006);
}

/* .swiper-slide img1 {
